* {
  box-sizing: border-box;
}

body {
  color: #212121;
  letter-spacing: .03em;
  background-color: #fff;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  line-height: 1.14;
}

body.lock {
  overflow: hidden;
}

a {
  text-decoration: none;
}

h1, h2, h3, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

