.about__title, .prise__title, .features__title, .masters__title {
  color: #303030;
  font-family: Raleway;
  font-size: 42px;
  font-weight: bold;
}

.suptitle-primary, .suptitle-secondary {
  color: #303030;
  font-size: 11px;
  font-weight: 600;
  line-height: 15px;
}

.suptitle-secondary {
  color: #9da4bd99;
}

.suptitle-primary:before, .suptitle-secondary:before {
  width: 60px;
  height: 1px;
  content: "";
  vertical-align: middle;
  background: #9da4bd99;
  margin-right: 20px;
  display: inline-block;
}

.suptitle-primary:before {
  background: #303030;
}

.auth-nav__btn, .about__btn, .price__btn, .footer__btn {
  color: #fff;
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  -o-transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  border: 1px solid #ff6c00;
  border-radius: 25px;
  padding: 12px 20px;
  font-size: 12px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.auth-nav__btn:hover, .about__btn:hover, .price__btn:hover, .footer__btn:hover, .auth-nav__btn:focus, .about__btn:focus, .price__btn:focus, .footer__btn:focus {
  background-color: #ff6c00;
}

